import "./App.css";
import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import Loader from "components/Loader/Loader";
import Index from "pages/Index/Index";
import Scribe from "pages/scribe/Scribe";
import Questionnaire from "pages/Questionnaire/Questionnaire";
import {
  setDataLinkType,
  setDataDemographicid,
  setPatientName,
  setPhoneNumber,
} from "Reducers/dataReducer/dataActions";
import useFormatPhoneNumber from "hooks/useFormatPhoneNumber";

const MainContent = () => {
  const [isLoading, setIsLoading] = useState(true);
  const dispatch = useDispatch();

  const { linkType, phoneNumber } = useSelector((state) => state.data);
  const { inProgress } = useSelector((state) => state.scribe);
  const [currentWindow] = useState(window.location.href);

  // States for selectedTab management
  const [selectedTab, setSelectedTab] = useState("");

  useEffect(() => {
    if (linkType === "Phone call") {
      setSelectedTab("Phone Appointment");
    } else if (linkType === "Video call") {
      setSelectedTab("Virtual Appointment");
    } else if (linkType === "Clinic") {
      setSelectedTab("Clinic");
    }
  }, [linkType]);

  // URLs for development; change to environment variables in production
  // const PROVIDER_WINDOW = "http://localhost:3000";
  const PROVIDER_WINDOW = process.env.REACT_APP_PROVIDER_WINDOW;
  const ENCOUNTER_WINDOW = process.env.REACT_APP_ENCOUNTER_WINDOW;

  useEffect(() => {
    if (linkType) {
      setIsLoading(false);
    } else {
      const timer = setTimeout(() => setIsLoading(false), 2000);
      return () => clearTimeout(timer);
    }
  }, [linkType]);

  const formattedPhoneNumber = useFormatPhoneNumber(phoneNumber);

  useEffect(() => {
    const handleMessage = (event) => {
      if (event.source !== window) return;

      if (event.data.type === "UPDATE_DEMOGRAPHIC_INFO") {
        console.log("Received Data:", event.data);
        const { demographicId, linkType, name, phoneNumber } = event.data;

        if (!inProgress) {
          dispatch(setDataDemographicid(demographicId));
          dispatch(setDataLinkType(linkType));
          dispatch(setPatientName(name));
          dispatch(setPhoneNumber(phoneNumber));
        }
      }
    };

    window.addEventListener("message", handleMessage);
    return () => window.removeEventListener("message", handleMessage);
  }, [dispatch, inProgress, formattedPhoneNumber, phoneNumber]);

  const renderComponent = () => {
    if (currentWindow.includes(PROVIDER_WINDOW)) {
      if (
        linkType === "Clinic" ||
        linkType === "Phone call" ||
        linkType === "Video call"
      ) {
        return (
          <Scribe
            key="Scribe"
            selectedTab={selectedTab}
            setSelectedTab={setSelectedTab}
          />
        );
      } else {
        return <Index key="Index" />;
      }
    }

    if (currentWindow.includes(ENCOUNTER_WINDOW)) {
      return <Questionnaire key="Questionnaire" />;
    }

    return <Index key="IndexFallback" />;
  };

  return <>{isLoading ? <Loader /> : renderComponent()}</>;
};

export default MainContent;
