import { updateButtonState } from 'utils/buttonUtils';
import { connectWebSocket, closeWebSocket, isWebSocketOpen } from './websocketService';

export const createTwilioCallService = (device, dispatch, actions) => {
  let currentConnection = null;
  const wssUrl = process.env.REACT_APP_WSS_URL;

  const makeCall = async (phoneNumber) => {
    try {
      if (!device) throw new Error("Twilio device is not initialized");
      if (!phoneNumber){
        return console.error("Phone number is required");
      } 

      console.log("Device:", device);
      console.log("Phone number:", phoneNumber);

      connectWebSocket(wssUrl, phoneNumber, dispatch, actions);
      const call = await device.connect({ params: { phone_number: phoneNumber } });
      if (!call) throw new Error("Failed to create call object");

      currentConnection = call;

      call.on("accept", () => {
        console.log("Call connected");
        console.log("Call : ", call)
        updateButtonState(dispatch, actions, { callButton: true, hangupButton: false, muteButton: false });
      });

      call.on("error", (error) => {
        console.error("Call error:", error);
        updateButtonState(dispatch, actions, { callButton: false, hangupButton: true, muteButton: true });
        closeWebSocket();
      });

      call.on("disconnect", () => {
        console.log("Call disconnected");
        updateButtonState(dispatch, actions, { callButton: false, hangupButton: true, muteButton: true });
        currentConnection = null;
        closeWebSocket();
      });

      call.on("reject", () => {
        console.error("Call rejected");
        closeWebSocket();
        updateButtonState(dispatch, actions, { callButton: false, hangupButton: true, muteButton: true });
      });

      return call;

    } catch (error) {
      console.error("Error during call process:", error);
      updateButtonState(dispatch, actions, { callButton: false, hangupButton: true, muteButton: true });
      throw error;
    }
  };

  const muteCall = (currentConnection) => {
    if (currentConnection) {
      const isMuted = currentConnection.isMuted();
      currentConnection.mute(!isMuted);
      dispatch(actions.setIsCallMuted(!isMuted));
      console.log(`Call ${!isMuted ? 'muted' : 'unmuted'}`);
    } else {
      console.warn("No active call to mute/unmute");
    }
  };

  const endCall = (currentConnection) => {
    if (currentConnection) {
      console.log("Ending current call");
      currentConnection.disconnect();
      currentConnection = null;
      dispatch(actions.setIsCallMuted(false));
      updateButtonState(dispatch, actions, { callButton: false, hangupButton: true, muteButton: true });
    } else {
      console.warn("No active call to end");
    }
    closeWebSocket();
  };

  return {
    getCurrentConnection: () => currentConnection,
    makeCall,
    muteCall,
    endCall,
  };
};
