import { CloseIcon, QuipoLogo } from "assets/Index";
import React from "react";
import "./Header.scss";
import { useDispatch, useSelector } from "react-redux";
import { stopRecording } from "Reducers/scribeReducer/scribeActions";
import { STOP_RECORDING } from "Reducers/scribeReducer/scribeActionConstants";

function Header() {
  const dispatch = useDispatch();
  const { isRecording } = useSelector((state) => state.scribe);
  const handleClose = () => {
    const appDiv = document.getElementById("react-chrome-extension");
    const appScript = document.getElementById("react-app-script");

    if (isRecording) {
      dispatch(stopRecording({ type: STOP_RECORDING, payload: false }));
    }

    try {
      appDiv?.parentNode?.removeChild(appDiv);
    } catch (error) {
      console.error("Error removing appDiv:", error);
    }

    try {
      appScript?.parentNode?.removeChild(appScript);
    } catch (error) {
      console.error("Error removing appScript:", error);
    }
  };

  return (
    <div className="qpeHeader">
      <div className="qpeSubHeader">
        <div className="qpeLeftContainer">
          <QuipoLogo />
          <h1 className="qpeHeaderH1">Quipo</h1>
        </div>
        <div className="qpeRightContainer">
          <button
            className="qpeCloseButton"
            onClick={handleClose}
            aria-label="Close"
          >
            <CloseIcon />
          </button>
        </div>
      </div>
    </div>
  );
}

export default Header;
