import { EditIcon } from "assets/Index";
import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { SET_TRANSCRIPT } from "Reducers/scribeReducer/scribeActionConstants";

const Transcript = () => {
  const dispatch = useDispatch();
  const [isEditing, setIsEditing] = useState(false);
  const { isRecording, transcriptText } = useSelector((state) => state.scribe);
  const [editableText, setEditableText] = useState(transcriptText);

  useEffect(() => {
    setEditableText(transcriptText);
  }, [transcriptText]);

  const handleEditToggle = () => {
    if (!isRecording) {
      setIsEditing((prev) => !prev);
    }
  };

  const handleTextChange = (e) => {
    setEditableText(e.target.value);
  };

  const handleBlur = () => {
    if (isEditing) {
      dispatch({ type: SET_TRANSCRIPT, payload: editableText });
      setIsEditing(false);
    }
  };

  return (
    <div className="qpeMajorTextContainer">
      <div className="qpeTextContainer custom-scrollbar">
        {isEditing ? (
          <textarea
            value={editableText}
            onChange={handleTextChange}
            onBlur={handleBlur}
            className="qpeEditableText custom-scrollbar"
          />
        ) : (
          <>
            {editableText ? (
              <>{editableText}</>
            ) : (
              <div className="qpeNoData">
                <p>No transcription available.</p>
              </div>
            )}
          </>
        )}
      </div>
      <div>
        {!isRecording && transcriptText && !isEditing && (
          <button className="qpeEditButton" onClick={handleEditToggle}>
            <EditIcon />
          </button>
        )}
      </div>
    </div>
  );
};

export default Transcript;
