import { useState, useEffect } from 'react';

const useFormatPhoneNumber = (phoneNumber) => {
  const [formattedPhoneNumber, setFormattedPhoneNumber] = useState('');

  // Function to clean and format the phone number
  const formatPhoneNumber = (number) => {
    const cleaned = number.replace(/\D/g, ''); // Remove all non-numeric characters
    const match = cleaned.match(/^(\d{3})(\d{3})(\d{4})$/);
    if (match) {
      return `(${match[1]}) ${match[2]}-${match[3]}`;
    }
    return cleaned; // Return the cleaned number if it doesn't match the expected format
  };

  useEffect(() => {
    if (phoneNumber) {
      setFormattedPhoneNumber(formatPhoneNumber(phoneNumber));
    }
  }, [phoneNumber]);

  return formattedPhoneNumber;
};

export default useFormatPhoneNumber;
