import React, { useState, useRef } from "react";
import { EditIcon, TickIcon } from "assets/Index";
import { useDispatch, useSelector } from "react-redux";
import { setSoapNote } from "Reducers/scribeReducer/scribeActions";
import { handleKeyDown, moveCaretToEnd } from "utils/Utils";

const Summary = ({ hasSoapNoteData }) => {
  const dispatch = useDispatch();
  const { soapNote } = useSelector((state) => state.scribe);
  const [editMode, setEditMode] = useState({
    subjective: false,
    objective: false,
    assessment: false,
    plan: false,
  });

  const [tempContent, setTempContent] = useState({
    subjective: [
      ...(soapNote.subjective || ["No data available for subjective"]),
    ],
    objective: [...(soapNote.objective || ["No data available for objective"])],
    assessment: [
      ...(soapNote.assessment || ["No data available for assessment"]),
    ],
    plan: [...(soapNote.plan || ["No data available for plan"])],
  });
  const listRefs = useRef({
    subjective: [],
    objective: [],
    assessment: [],
    plan: [],
  });

  const handleEditClick = (section) =>
    setEditMode({ ...editMode, [section]: true });

  const handleSave = (section) => {
    const updatedContent = tempContent[section].filter(
      (item) => item.trim() !== ""
    );
    if (updatedContent.length === 0) {
      setTempContent((prev) => ({
        ...prev,
        [section]: ["No data available for " + section],
      }));
    } else {
      dispatch(setSoapNote({ ...soapNote, [section]: updatedContent }));
    }
    setEditMode((prev) => ({ ...prev, [section]: false }));
  };

  const handleContentChange = (section, index, value) => {
    const updated = [...tempContent[section]];
    updated[index] = value;
    setTempContent({ ...tempContent, [section]: updated });
  };

  const handleInput = (section, index, e) => {
    handleContentChange(section, index, e.target.innerText);
    moveCaretToEnd(e);
  };

  const handleNoDataClick = (section) => {
    setEditMode((prev) => ({ ...prev, [section]: true }));
    setTempContent((prev) => ({ ...prev, [section]: [""] }));
  };

  return (
    <div className="qpeMajorTextContainer">
      <div className="qpeTextContainer custom-scrollbar">
        {hasSoapNoteData ? (
          ["subjective", "objective", "assessment", "plan"].map((section) => (
            <div key={section} className={`qpeSection qpeSection-${section}`}>
              <div className="qpeSoapHeader">
                <h4 className="qpeSectionTitle">
                  {section.charAt(0).toUpperCase() + section.slice(1)}:
                </h4>
                <button
                  className="qpeEditorButton"
                  onClick={() =>
                    editMode[section]
                      ? handleSave(section)
                      : handleEditClick(section)
                  }
                  style={{ marginLeft: "10px" }}
                >
                  {editMode[section] ? <TickIcon /> : <EditIcon />}
                </button>
              </div>
              <ul className="qpeList">
                <div
                  className={`qpeListItemContainer ${
                    editMode[section] ? "editMode" : ""
                  }`}
                >
                  {(tempContent[section] || []).length === 0 ||
                  tempContent[section][0] ===
                    "No data available for " + section ? (
                    <li
                      className="qpeListItem noData"
                      contentEditable={editMode[section]}
                      suppressContentEditableWarning
                      onClick={() => handleNoDataClick(section)}
                      onInput={(e) => handleInput(section, 0, e)}
                      style={{
                        cursor: editMode[section] ? "text" : "pointer",
                        outline: "none",
                      }}
                    >
                      {editMode[section]
                        ? ""
                        : "No data available for " + section}
                    </li>
                  ) : (
                    (tempContent[section] || []).map((item, index) => (
                      <li key={index} className="qpeListItem">
                        {editMode[section] ? (
                          <div
                            className="qpeEditableItem"
                            contentEditable
                            suppressContentEditableWarning
                            onInput={(e) => handleInput(section, index, e)}
                            onKeyDown={(e) =>
                              handleKeyDown(
                                section,
                                index,
                                tempContent,
                                setTempContent,
                                listRefs,
                                e
                              )
                            }
                            ref={(el) =>
                              (listRefs.current[section][index] = el)
                            }
                            onFocus={(e) => moveCaretToEnd(e)}
                            style={{
                              width: "100%",
                              resize: "none",
                              outline: "none",
                            }}
                          >
                            {item}
                          </div>
                        ) : (
                          item
                        )}
                      </li>
                    ))
                  )}
                </div>
              </ul>
            </div>
          ))
        ) : (
          <div className="qpeNoData">
            <p>No data available</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default Summary;
