import React from "react";
import Transcript from "components/Transcript/Transcript";
import Summary from "components/SoapNote/SoapNote";
import AppointmentScreen from "AppointmentScreen/AppointmentScreen";

const TabContent = ({
  selectedTab,
  timer,
  patientName,
  loading,
  hasSoapNoteData,
  actions,
  dispatch,
  twilioService,
  setTwilioService,
  phoneNumber,
}) => {
  const renderTabContent = () => {
    if (selectedTab === "AI Summary" && loading)
      return <div className="loader">Loading SOAP Note...</div>;

    switch (selectedTab) {
      case "session":
      case "transcript":
      case "Clinic":
      case "Phone Appointment":
        return <AppointmentScreen
          timer={timer}
          patientName={patientName}
          actions={actions}
          dispatch={dispatch}
          twilioService={twilioService}
          setTwilioService={setTwilioService}
          phoneNumber={phoneNumber}
        />
      case "Virtual Appointment":
        return <AppointmentScreen timer={timer} patientName={patientName} />;
      case "Dictation":
        return <Transcript />;
      case "AI Summary":
        return <Summary hasSoapNoteData={hasSoapNoteData} />;
      default:
        return null;
    }
  };

  return (
    <div className="qpeInnerContainer custom-scrollbar">
      {renderTabContent()}
    </div>
  );
};

export default TabContent;
