import React from "react";
import { createRoot } from "react-dom/client";
import App from "./App";

// Function to initialize React app
const initializeReactApp = (elementId) => {
  const rootElement = document.getElementById(elementId);
  if (rootElement) {
    const root = createRoot(rootElement);
    root.render(
      <React.StrictMode>
        <App />
      </React.StrictMode>
    );
    console.log(`React app rendered in ${elementId}`);
    return true;
  }
  return false;
};

// Try to initialize the app in the preferred root
if (!initializeReactApp("react-chrome-extension")) {
  // Fallback to secondary root if preferred root is missing
  if (!initializeReactApp("root")) {
    console.error(
      "No div found to render the React app in either root elements"
    );
  }
}
