import React from "react";
import { BarIcon, PHNIcon, PhoneIcon } from "assets/Index";
import { getInitials } from "utils/Utils";

const PatientDetails = ({ patientName, demoGraphicId, phoneNumber }) => {
  return (
    <div className="qpeDetailsContainer">
      <div className="qpeSubDetailsContainer">
        <div className="qpeDetailsLeftContainer">
          <div className="qpeProfilePic">
            <p>{getInitials(patientName) || "N/A"}</p>
          </div>
        </div>
        <div className="qpeDetailsRightContainer">
          <p className="qpePatientName">{patientName || "Name"}</p>
          <div className="qpePatientDataContainer">
            <div className="qpePatientData">
              <PHNIcon />
              <p>{demoGraphicId || "Demo"}</p>
            </div>
            <BarIcon />
            <div className="qpePatientData">
              <PhoneIcon />
              <p>{phoneNumber || "Phone "}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PatientDetails;
