import { createTwilioTokenService } from "./twilioTokenService";
import { createTwilioDeviceService } from "./twilioDeviceService";
import { createTwilioCallService } from "./twilioCallService";

export const createTwilioService = (backendUrl, dispatch, actions, isCallActive, currentCall, setCurrentCall) => {
  let tokenService = null;
  let deviceService = null;
  let callService = null;
  let device

  const initialize = async () => {
    try {
      tokenService = createTwilioTokenService(backendUrl);
      deviceService = await createTwilioDeviceService(dispatch, actions);

      const token = await tokenService.fetchToken();
      device = await deviceService.setupDevice(token);

      callService = createTwilioCallService(device, dispatch, actions, isCallActive, currentCall, setCurrentCall);
      console.log("Twilio Service initialized successfully.");
    } catch (error) {
      console.error("Failed to initialize Twilio Service:", error);
      throw new Error("Twilio Service initialization failed.");
    }
  };

  const makeCall = (phoneNumber) => {
    if (callService) {
      callService.makeCall(phoneNumber);
    } else {
      console.error("Call service is not initialized.");
    }
  };

  const muteCall = () => {
    if (callService) {
      callService.muteCall();
    } else {
      console.error("Call service is not initialized.");
    }
  };

  const endCall = () => {
    if (callService) {
      callService.endCall();
    } else {
      console.error("Call service is not initialized.");
    }
  };

  return {
    initialize,
    makeCall,
    muteCall,
    endCall
  };
};