import React from "react";
import "./Index.scss";
import { useSelector, useDispatch } from "react-redux";
import { setDataLinkType } from "../../Reducers/dataReducer/dataActions";

function Index() {
  const dispatch = useDispatch();
  const linkType = useSelector((state) => state.data.linkType);

  return (
    <div className="qpeIndex-container">
      <h1 className="qpeIndex-heading">Data available: {linkType || "None"}</h1>
      <div className="qpeButton-container">
        <button
          className="qpeIndex-button"
          onClick={() => dispatch(setDataLinkType("Video call"))}
        >
          Direct to Video
        </button>
        <button
          className="qpeIndex-button"
          onClick={() => dispatch(setDataLinkType("Phone call"))}
        >
          Direct to Phone
        </button>
        <button
          className="qpeIndex-button"
          onClick={() => dispatch(setDataLinkType("Clinic"))}
        >
          Direct to Inclinic
        </button>
      </div>
    </div>
  );
}

export default Index;
