import "./Scribe.scss";
import { useState, useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  startRecording,
  stopRecording,
  pauseRecording,
  resumeRecording,
  setSoapNote,
  setTranscript,
} from "Reducers/scribeReducer/scribeActions";
import { fetchSoapNote } from "services/soapNoteService";
import Controllers from "components/Controllers/Controllers";
import PatientDetails from "components/PatientDetails/PatientDetails";
import TabNavigation from "components/TabNavigation/TabNavigation";
import TabContent from "components/TabContent/TabContent";
import { setCallButtonDisabled, setDeviceReady, setHangupButtonDisabled, setIsCallActive, setIsCallMuted, setIsCallRinging, setMuteButtonDisabled } from "Reducers/twilioReducer/twilioActions";
const Scribe = ({ selectedTab, setSelectedTab }) => {
  const dispatch = useDispatch();
  const { isRecording, isPaused, transcriptText, soapNote } = useSelector(
    (state) => state.scribe
  );
  const { patientName, demoGraphicId, phoneNumber, linkType } = useSelector(
    (state) => state.data
  );
  const { isDeviceReady, callButtonDisabled, hangupButtonDisabled, muteButtonDisabled, isCallRinging } = useSelector((state) => state.twilio);

  const [timer, setTimer] = useState(0);
  const [loading, setLoading] = useState(false);
  const [isStopping, setIsStopping] = useState(false);
  const [hasRecordingStarted, setHasRecordingStarted] = useState(false);
  const [twilioService, setTwilioService] = useState(null);

  const previousTranscript = useRef(transcriptText);
  const timerIntervalRef = useRef(null);

  const actions = {
    setDeviceReady: (ready) => dispatch(setDeviceReady(ready)),
    setCallButtonDisabled: (disabled) => dispatch(setCallButtonDisabled(disabled)),
    setHangupButtonDisabled: (disabled) => dispatch(setHangupButtonDisabled(disabled)),
    setMuteButtonDisabled: (disabled) => dispatch(setMuteButtonDisabled(disabled)),
    setIsCallMuted: (muted) => dispatch(setIsCallMuted(muted)),
    setTranscript: (text) => dispatch(setTranscript(text)),
    setIsCallRinging: (text) => dispatch(setIsCallRinging(text)),
    setIsCallActive: (active) => dispatch(setIsCallActive(active))
  };

  useEffect(() => {
    if (isRecording && !isPaused) {
      timerIntervalRef.current = setInterval(
        () => setTimer((t) => t + 1),
        1000
      );
    }
    return () => clearInterval(timerIntervalRef.current);
  }, [isRecording, isPaused]);

  const handleRecording = (action) => {
    if (action === stopRecording) {
      setIsStopping(true);
      clearInterval(timerIntervalRef.current);
      dispatch(action());
    } else {
      if (action === startRecording && !hasRecordingStarted) {
        setTimer(0);
        setHasRecordingStarted(true);
      }
      dispatch(action());
    }
  };

  useEffect(() => {
    if (isStopping && !isRecording) setIsStopping(false);
  }, [isRecording, isStopping]);

  useEffect(() => {
    if (
      !isRecording &&
      selectedTab === "AI Summary" &&
      transcriptText !== previousTranscript.current
    ) {
      previousTranscript.current = transcriptText;
      setLoading(true);
      fetchSoapNote(transcriptText).then((result) => {
        dispatch(setSoapNote(result?.soapnote || {}));
        setLoading(false);
      });
    }
  }, [isRecording, selectedTab, transcriptText, dispatch]);

  const hasSoapNoteData =
    soapNote !== undefined &&
    ((soapNote.subjective || []).length > 0 ||
      (soapNote.objective || []).length > 0 ||
      (soapNote.assessment || []).length > 0 ||
      (soapNote.plan || []).length > 0);

  return (
    <div className="qpeIndexMainContainer">
      <div className="qpeScribe-container">
        <div className="qpeTop-container">
          <PatientDetails
            patientName={patientName}
            demoGraphicId={demoGraphicId}
            phoneNumber={phoneNumber}
          />
          <TabNavigation
            selectedTab={selectedTab}
            setSelectedTab={setSelectedTab}
          />
        </div>
        <div className="qpeOuterContainer custom-srollbar">
          <div className="qpeContextContainer">
            <TabContent
              selectedTab={selectedTab}
              timer={timer}
              patientName={patientName}
              soapNote={soapNote}
              loading={loading}
              hasSoapNoteData={hasSoapNoteData}
              actions={actions}
              dispatch={dispatch}
              setTwilioService={setTwilioService}
              twilioService={twilioService}
              phoneNumber={phoneNumber}
            />
          </div>
        </div>
        {(selectedTab === "Clinic" ||
          selectedTab === "Phone Appointment" ||
          (selectedTab === "summary" && hasSoapNoteData) ||
          selectedTab === "Virtual Appointment") && (
            <Controllers
              selectedTab={selectedTab}
              isRecording={isRecording}
              isPaused={isPaused}
              isStopping={isStopping}
              onRecordStart={() => handleRecording(startRecording)}
              onRecordPause={() => handleRecording(pauseRecording)}
              onRecordStop={() => handleRecording(stopRecording)}
              onRecordResume={() => handleRecording(resumeRecording)}
              twilioService={twilioService}
              phoneNumber={phoneNumber}
              isDeviceReady={isDeviceReady}
              callButtonDisabled={callButtonDisabled}
              hangupButtonDisabled={hangupButtonDisabled}
              muteButtonDisabled={muteButtonDisabled}
            />
          )}
      </div>
    </div>
  );
};

export default Scribe;
