  isRecording: false,
  isPaused: false,
  error: null,
  inProgress: false,
  // soapNote: {
  //   subjective: [
  //     "Patient reports feeling off lately with headaches for the past week.",
  //     "Headaches are described as a constant dull pain, sometimes sharp, rated 6-7/10 in severity.",
  //     "Patient attributes headaches to long hours of screen time, noting they worsen after extended computer use.",
