import React from "react";
import PropTypes from "prop-types";
import { useSelector } from "react-redux";

const TabNavigation = ({ selectedTab, setSelectedTab }) => {
  const { linkType } = useSelector((state) => state.data);

  // Helper function to determine tab names based on linkType
  const getTabs = () => {
    if (linkType === "Clinic") return ["Clinic", "Dictation", "AI Summary"];
    if (linkType === "Phone call")
      return ["Phone Appointment", "Dictation", "AI Summary"];
    if (linkType === "Video call")
      return ["Virtual Appointment", "Dictation", "AI Summary"];
    return [];
  };

  const tabs = getTabs();

  return (
    <div className="qpeTabContainer">
      {tabs.map((tab) => (
        <div
          key={tab}
          className={`qpeTabOption ${
            selectedTab === tab ? "qpeSelectedTab" : ""
          }`}
          onClick={() => setSelectedTab(tab)}
        >
          {tab.charAt(0).toUpperCase() + tab.slice(1)}
        </div>
      ))}
    </div>
  );
};

TabNavigation.propTypes = {
  selectedTab: PropTypes.string.isRequired,
  setSelectedTab: PropTypes.func.isRequired,
};

export default TabNavigation;
