let socket = null;

export const connectWebSocket = (url, phoneNumber, dispatch, actions) => {
  if (socket && socket.readyState === WebSocket.OPEN) {
    console.log("Closing existing WebSocket connection");
    socket.close();
  }
  const fullUrl = url + phoneNumber.replace("+", "");
  socket = new WebSocket(fullUrl);
  console.log(socket)
  socket.onmessage = (e) => {
    try {
      const messageData = JSON.parse(e.data);
      if (messageData.transcription) {
        console.log("Transcription:", messageData.transcription);
        dispatch(actions.setTranscript(messageData.transcription));
      } else {
        console.log("Received WebSocket message:", messageData);
      }
    } catch (error) {
      console.error("Error parsing WebSocket message:", error);
    }
  };

  socket.onopen = () => {
    console.log("WebSocket connection opened");
  };

  socket.onclose = () => {
    console.log("WebSocket connection closed");
    socket = null;
  };

  socket.onerror = (error) => {
    console.error("WebSocket error:", error);
  };
};

export const closeWebSocket = () => {
  if (socket) {
    console.log("Closing WebSocket connection");
    socket.close();
    socket = null;
  }
};

export const isWebSocketOpen = () => {
  return socket && socket.readyState === WebSocket.OPEN;
};
