import React from "react";
import {
  MailIcon,
  MicIcon,
  ResumeIcon,
  UploadDocument,
  EndCallIcon,
  CallMicIcon,
  HangUpicon,
  PauseIcon,
} from "assets/Index";

const Controllers = ({
  isRecording,
  isPaused,
  isStopping,
  onRecordStart,
  onRecordPause,
  onRecordStop,
  onRecordResume,
  selectedTab,
  linkType,
  isDeviceReady,
  muteButtonDisabled,
  hangupButtonDisabled,
  twilioService,
  phoneNumber,
}) => {
  const renderRecordingControls = () => {
    if (isStopping) {
      return (
        <button className="qpeScribe-button qpeScribe-disabled" disabled>
          Stopping...
        </button>
      );
    }
    if (!isRecording) {
      return (
        <button
          onClick={onRecordStart}
          className="qpeScribe-button micIcon"
          aria-label="Start Recording"
        >
          <MicIcon />
        </button>
      );
    }
    if (isPaused) {
      return (
        <button
          onClick={onRecordResume}
          className="qpeScribe-button resumeIcon"
          aria-label="Resume Recording"
        >
          <ResumeIcon />
        </button>
      );
    }
    return (
      <>
        <button
          onClick={onRecordPause}
          className="qpeScribe-button pauseIcon"
          aria-label="Pause Recording"
        >
          <PauseIcon />
        </button>
        <button
          onClick={onRecordStop}
          className="qpeScribe-button hangupIcon"
          aria-label="Stop Recording"
        >
          <HangUpicon />
        </button>
      </>
    );
  };

  const renderPhoneAppointmentControls = () => {
    if (selectedTab === "Phone Appointment") {
      return (
        <>
          <button
            onClick={() => twilioService?.muteCall()}
            className={`qpeScribe-button micIcon ${muteButtonDisabled ? "disabled" : ""
              }`}
            disabled={!isDeviceReady || muteButtonDisabled}
            aria-label="Mute Call"
          >
            <CallMicIcon />
          </button>
          <button
            onClick={() => twilioService?.endCall()}
            className={`qpeScribe-button hangupIcon ${hangupButtonDisabled ? "disabled" : ""
              }`}
            disabled={!isDeviceReady || hangupButtonDisabled}
            aria-label="End Call"
          >
            <EndCallIcon />
          </button>
        </>
      );
    }
    return null;
  };

  const renderClinicAppointmentControls = () => {
    if (selectedTab === "Clinic") {
      return renderRecordingControls();
    }
    return null;
  };

  const renderSummaryControls = () => {
    if (selectedTab === "AI Summary") {
      return (
        <>
          <button className="qpeScribe-button mailIcon" aria-label="Send Mail">
            <MailIcon />
          </button>
          <button
            className="qpeScribe-button uploadIcon"
            aria-label="Upload Document"
          >
            <UploadDocument />
          </button>
        </>
      );
    }
    return null;
  };

  const renderVideoAppointmentControls = () => {
    if (selectedTab === "Virtual Appointment") {
      return (
        <div className="space-for-video-call">
          <button className="qpePhone-button">Start</button>
        </div>
      );
    }
    return null;
  };

  return (
    <div className="qpeButtonContainer">
      {renderClinicAppointmentControls()}
      {renderPhoneAppointmentControls()}
      {renderSummaryControls()}
      {renderVideoAppointmentControls()}
    </div>
  );
};

export default Controllers;
