import PatientProfile from "components/PatientProfile/PatientProfile";
import TimerDisplay from "components/TimerDisplay/TimerDisplay";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { createTwilioService } from "services/twilio/twilioService";


const AppointmentScreen = ({ selectedTab, timer, patientName, actions, dispatch, setTwilioService, twilioService, phoneNumber }) => {
  const { linkType } = useSelector((state) => state.data);
  const { isCallActive } = useSelector((state) => state.twilio);
  const backendUrl = process.env.REACT_APP_BACKEND_URL;
  const [currentCall, setCurrentCall] = useState({})

  useEffect(() => {
    let interval = null;
    if (timer > 0) {
      interval = setInterval(() => { }, 1000);
    } else {
      clearInterval(interval);
    }
    return () => clearInterval(interval);
  }, [timer]);

  useEffect(() => {
    if (linkType === "Phone call") {
      const initTwilioService = async () => {
        try {
          console.log('Initializing Twilio Service...');
          const service = createTwilioService(backendUrl, dispatch, actions, currentCall, setCurrentCall);
          await service.initialize();
          setTwilioService(service)
        } catch (err) {
          console.error('Error initializing Twilio Service:', err);
        }
      };

      initTwilioService();
    }
  }, []);

  const phoneCall = () => {
    if (!isCallActive) {
      if (linkType === "Phone call") {
        twilioService.makeCall(phoneNumber);
        dispatch(actions.setIsCallActive(true))
      }
    }
  }

  useEffect(() => {
    if (linkType === "Phone call" && twilioService) {
      try {
        if (!isCallActive) {
          phoneCall();
          console.log("Calling Patient...")
        }
      } catch (error) {
        console.error("Failed to make a call:", error);
      }
    }
  }, [twilioService]);


  return (
    <div
      className={`qpeSessionData ${selectedTab === "Clinic" ? "qpeBorder-None" : ""
        }`}
    >
      <TimerDisplay timer={timer} />
      {linkType !== "Video call" && (
        <PatientProfile patientName={patientName} />
      )}
    </div>
  );
};

export default AppointmentScreen;
