import { scribeFactory } from "../../services/scribe";
import {
  START_RECORDING,
  STOP_RECORDING,
  PAUSE_RECORDING,
  RESUME_RECORDING,
  SET_TRANSCRIPT,
  SET_ERROR,
  SET_SOAP_NOTE,
  SET_IN_PROGRESS,
} from "./scribeActionConstants";

let processorRef = null;

let lastTranscriptPortion = "";

function createCallback(dispatch, getState) {
  return (data) => {
    if (data && typeof data === "object") {
      const sortedValues = Object.entries(data)
        .sort(([keyA], [keyB]) => Number(keyA) - Number(keyB))
        .map(([_, value]) => value);
      const newTranscript = sortedValues.join(" ").trim();

      const { transcriptText } = getState().scribe;

      if (newTranscript !== lastTranscriptPortion) {
        const freshContent = newTranscript
          .replace(lastTranscriptPortion, "")
          .trim();

        if (freshContent) {
          const updatedTranscript = transcriptText
            ? `${transcriptText} ${freshContent}`
            : freshContent;

          dispatch({ type: SET_TRANSCRIPT, payload: updatedTranscript });

          lastTranscriptPortion = newTranscript;
        }
      }
    } else {
      console.error("Invalid data received:", data);
      dispatch({ type: SET_ERROR, payload: "Invalid data received" });
    }
  };
}

export const startRecording = () => async (dispatch, getState) => {
  dispatch({ type: SET_IN_PROGRESS, payload: true });
  try {
    const mode = localStorage.getItem("API_MODE") || "default";
    const ScribeProcessor = scribeFactory({ live: mode });

    processorRef = new ScribeProcessor(
      process.env.REACT_APP_WS_URL,
      process.env.REACT_APP_SERVER_URL,
      createCallback(dispatch, getState)
    );

    await processorRef.getMicrophoneAccess(
      () => {
        processorRef.startRecording();
        dispatch({ type: START_RECORDING });
        dispatch({ type: SET_ERROR, payload: null });
        dispatch({ type: SET_IN_PROGRESS, payload: false });
      },
      (error) => {
        console.error("Error while accessing microphone:", error);
        dispatch({ type: SET_ERROR, payload: "Microphone access error" });
        dispatch({ type: SET_IN_PROGRESS, payload: false });
      }
    );
  } catch (error) {
    console.error("Error starting recording:", error);
    dispatch({ type: SET_ERROR, payload: "Failed to start recording" });
    dispatch({ type: SET_IN_PROGRESS, payload: false });
  }
};

export const stopRecording = () => async (dispatch, getState) => {
  if (!processorRef) {
    dispatch({ type: SET_ERROR, payload: "Processor not initialized" });
    return;
  }

  dispatch({ type: SET_IN_PROGRESS, payload: true });
  try {
    const newTranscript = await processorRef.stopRecording();
    const { transcriptText } = getState().scribe;

    // Ensure newTranscript is not null or undefined before calling trim()
    const safeNewTranscript = newTranscript ? newTranscript.trim() : "";

    // If there's an existing transcript, avoid repeating parts of it
    let updatedTranscript = transcriptText;

    // Check if newTranscript is already part of transcriptText
    if (transcriptText && !transcriptText.endsWith(safeNewTranscript)) {
      updatedTranscript = transcriptText + " " + safeNewTranscript;
    }

    console.log("old::", transcriptText);
    console.log(updatedTranscript);

    dispatch({ type: STOP_RECORDING, payload: updatedTranscript });
    dispatch({ type: SET_TRANSCRIPT, payload: updatedTranscript }); // Update to the new transcript

    lastTranscriptPortion = safeNewTranscript; // Save the current new transcript portion
    processorRef = null;
    dispatch({ type: SET_IN_PROGRESS, payload: false });
  } catch (error) {
    console.error("Error stopping recording:", error);
    dispatch({ type: SET_ERROR, payload: "Failed to stop recording" });
    dispatch({ type: SET_IN_PROGRESS, payload: false });
  }
};

export const pauseRecording = () => (dispatch) => {
  if (processorRef) {
    dispatch({ type: SET_IN_PROGRESS, payload: true });
    processorRef.pauseRecording();
    console.log("Dispatching PAUSE_RECORDING");
    dispatch({ type: PAUSE_RECORDING });
    dispatch({ type: SET_IN_PROGRESS, payload: false });
  } else {
    dispatch({ type: SET_ERROR, payload: "Processor not initialized" });
  }
};

export const resumeRecording = () => (dispatch) => {
  if (processorRef) {
    dispatch({ type: SET_IN_PROGRESS, payload: true });
    processorRef.resumeRecording();
    console.log("Dispatching RESUME_RECORDING");
    dispatch({ type: RESUME_RECORDING });
    dispatch({ type: SET_IN_PROGRESS, payload: false });
  } else {
    dispatch({ type: SET_ERROR, payload: "Processor not initialized" });
  }
};

export const setTranscript = (text) => ({
  type: SET_TRANSCRIPT,
  payload: text,
});

export const setSoapNote = (soapNote) => {
  return {
    type: SET_SOAP_NOTE,
    payload: soapNote,
  };
};

export const closeExtension = () => async (dispatch) => {
  dispatch({ type: SET_IN_PROGRESS, payload: true });
  if (processorRef) {
    processorRef.closeWSConnection();
    console.log("WebSocket connection closed.");

    await dispatch(stopRecording());
  }

  const appDiv = document.getElementById("react-chrome-extension");
  const appScript = document.getElementById("react-app-script");

  try {
    appDiv?.parentNode?.removeChild(appDiv);
    console.log("React app container removed.");
  } catch (error) {
    console.error("Error removing appDiv:", error);
  }

  try {
    appScript?.parentNode?.removeChild(appScript);
    console.log("React app script removed.");
  } catch (error) {
    console.error("Error removing appScript:", error);
  }

  dispatch({ type: SET_ERROR, payload: null });
  dispatch({
    type: SET_SOAP_NOTE,
    payload: { subjective: [], objective: [], assessment: [], plan: [] },
  });
  dispatch({ type: SET_IN_PROGRESS, payload: false });
};
