export const moveCaretToEnd = (e) => {
  const range = document.createRange();
  const selection = window.getSelection();
  range.selectNodeContents(e.target);
  range.collapse(false);
  selection.removeAllRanges();
  selection.addRange(range);
};

export const handleKeyDown = (
  section,
  index,
  tempContent,
  setTempContent,
  listRefs,
  e
) => {
  if (e.key === "Enter") {
    e.preventDefault();
    const updated = [...tempContent[section]];
    updated.splice(index + 1, 0, "");
    setTempContent((prev) => ({ ...prev, [section]: updated }));

    setTimeout(() => listRefs.current[section][index + 1]?.focus(), 0);
  } else if (e.key === "Backspace" && tempContent[section][index] === "") {
    e.preventDefault();
    const updated = [...tempContent[section]];
    updated.splice(index, 1);
    setTempContent((prev) => ({ ...prev, [section]: updated }));
    setTimeout(
      () =>
        listRefs.current[section][index - 1]?.focus() ||
        listRefs.current[section][index]?.focus(),
      0
    );
  }
};

export const getInitials = (name) => {
  if (!name) return "";
  return name
    .split(" ")
    .map((word) => word.charAt(0))
    .join("")
    .toUpperCase();
};
