import Tick from "./png/tick.png";
import Profile from "./png/profilePic.png";
import "./index.scss";

const big = 20;
const small = 16;
const enable = "";
const disable = "";

// Close Icon
export const CloseIcon = ({ fill = "#000", stroke = "#000" }) => {
  return (
    <svg
      width={small}
      height={small}
      viewBox="0 0 14 14"
      fill={fill}
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M13 1L1 13"
        stroke={stroke}
        strokeWidth="2"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
      <path
        d="M1 1L13 13"
        stroke={stroke}
        strokeWidth="2"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    </svg>
  );
};

export const TickIcon = () => {
  return <img src={Tick} alt="Tick Icon" style={{ width: big, height: big }} />;
};

