import { Device } from '@twilio/voice-sdk';
import { updateButtonState } from 'utils/buttonUtils';

export const createTwilioDeviceService = (dispatch, actions) => {
  let device;

  const setupDevice = async (token) => {
    device = new Device(token);
    device.register()

    dispatch(actions.setDeviceReady(true));

    device.on('deviceReady', () => {
      console.log("DEVICE >>> ",device)
      console.log('Device is ready');
    });

    device.on('deviceDisconnected', () => {
      console.log('Device disconnected');
    });

    device.on('error', (error) => {
      console.error('Error occurred:', error);
    });
    updateButtonState(dispatch, actions, {
      callButton: false, hangupButton: false, muteButton: false,
    });
    return device;
  };

  return {
    setupDevice,
    updateButtonState
  };
};