import { SET_DEVICE_READY, SET_CALL_BUTTON_DISABLED, SET_HANGUP_BUTTON_DISABLED, SET_MUTE_BUTTON_DISABLED, SET_IS_CALL_MUTED, SET_IS_CALL_RINGING, SET_IS_CALL_ACTIVE } from './twilioActionConstants';

const initialState = {
  isDeviceReady: false,
  callButtonDisabled: false,
  hangupButtonDisabled: true,
  muteButtonDisabled: true,
  isCallMuted: false,
  isCallRinging: false,
  isCallActive: false,
};

const twilioReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_DEVICE_READY:
      return { ...state, isDeviceReady: action.payload };
    case SET_CALL_BUTTON_DISABLED:
      return { ...state, callButtonDisabled: action.payload };
    case SET_HANGUP_BUTTON_DISABLED:
      return { ...state, hangupButtonDisabled: action.payload };
    case SET_MUTE_BUTTON_DISABLED:
      return { ...state, muteButtonDisabled: action.payload };
    case SET_IS_CALL_MUTED:
      return { ...state, isCallMuted: action.payload };
    case SET_IS_CALL_RINGING:
      return { ...state, isCallRinging: action.payload };
    case SET_IS_CALL_ACTIVE:
      return { ...state, isCallActive: action.payload };
    default:
      return state;
  }
};

export default twilioReducer;
