import { SET_DEVICE_READY, SET_CALL_BUTTON_DISABLED, SET_HANGUP_BUTTON_DISABLED, SET_MUTE_BUTTON_DISABLED, SET_IS_CALL_MUTED, SET_IS_CALL_RINGING, SET_IS_CALL_ACTIVE } from './twilioActionConstants';

export const setDeviceReady = (isReady) => ({
  type: SET_DEVICE_READY,
  payload: isReady,
});

export const setCallButtonDisabled = (isDisabled) => ({
  type: SET_CALL_BUTTON_DISABLED,
  payload: isDisabled,
});

export const setHangupButtonDisabled = (isDisabled) => ({
  type: SET_HANGUP_BUTTON_DISABLED,
  payload: isDisabled,
});

export const setMuteButtonDisabled = (isDisabled) => ({
  type: SET_MUTE_BUTTON_DISABLED,
  payload: isDisabled,
});

export const setIsCallMuted = (isMuted) => ({
  type: SET_IS_CALL_MUTED,
  payload: isMuted,
});

export const setIsCallRinging = (isRinging) => ({
  type: SET_IS_CALL_RINGING,
  payload: isRinging,
})

export const setIsCallActive = (isActive) => ({
  type: SET_IS_CALL_ACTIVE,
  payload: isActive,
})